.battle {
    color: rgba(75, 57, 110, 0.795);
}

.track {
    color: rgba(158, 62, 24, 0.87);
}

.favorite {
    color: rgba(129, 173, 27, 0.822);
}

.complete {
    color: rgba(71, 75, 61, 0.295);
}

.current {
    animation: blinker 1s linear infinite;
}

.playlist {
    height: 300px;
    overflow-y: scroll;
}

/* width */

::-webkit-scrollbar {
    width: 4px;
}

/* Track */

::-webkit-scrollbar-track {
    box-shadow: inset 0 0 5px rgba(13, 1, 117, 0.6);
    border-radius: 10px;
}

/* Handle */

::-webkit-scrollbar-thumb {
    background: rgba(13, 1, 117, 0.8);
    border-radius: 10px;
}

/* Handle on hover */

::-webkit-scrollbar-thumb:hover {
    background: #720101c2;
}

.my-icon{
    cursor: pointer;
}
.my-icon:hover{
    color:rgba(94, 0, 0, 0.7);
}

@keyframes blinker {
    50% {
        opacity: 0;
    }
}

@media screen and (max-width: 767px) {
    .nomobile {
        display: none;
    }
    .mytext {
        font-size: 12px;
    }
}